<template>
	<view class="container">
		<!-- 富文本框 -->
		<view class="rich-box-view">
			<rich-text :nodes="nodes"></rich-text>
			<rich-text :nodes="strings"></rich-text>
		</view>
		<view class="editor-box">
			<editor id="editor" class="ql-container" :placeholder="placeholder" @ready="onEditorReady"></editor>
			<button type="warn" @tap="undo">撤销</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				placeholder: '开始输入...',
				nodes: [{
						name: 'div',
						attrs: {
							class: 'div-class',
							style: 'line-height: 20px; color: red; text-align:center;'
						},
						children: [{
							type: 'text',
							text: 'Hello&nbsp;uni-app!'
						}]
					},
					{
						name: 'i',
						attrs: {
							class: 'class',
							style: 'line-height: 20px; color: blue; text-align:center;'
						},
						children: [{
							type: 'text',
							text: 'Hello&nbsp;uni-app!'
						}]
					}
				],
				strings: '<div style="text-align:center;"><img src="https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png"/></div>'
			}
		},
		methods: {
			onEditorReady() {
				uni.createSelectorQuery().select('#editor').context((res) => {
					this.editorCtx = res.context
				}).exec()
			},
			undo() {
				this.editorCtx.undo()
			}
		}
	}
</script>

<style scoped lang="scss">
	.container {
		padding: 10px;
		
		.editor-box {
			#editor {
				width: 100%;
				height: 300px;
				background-color: #CCCCCC;
			}
			
			button {
				margin-top: 10px;
			}
		}
	}

	
</style>
